<template>
  <div>
    <el-form>
      <!-- 上半部分 -->
      <el-form-item label="是否启用加密">
        <el-switch v-model="BackupParams.enableEnc"></el-switch>
      </el-form-item>
      <el-form-item label="密钥" v-show="BackupParams.enableEnc">
        <el-input v-model="BackupParams.password"
                  placeholder="输入密钥" type="password" show-password
                  clearable style="width: 400px;"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="handleBackup" style="width: 430px;">备份并下载</el-button>
      </el-form-item>

      <el-divider class="divider"/>

      <!-- 下半部分 -->
      <el-text class="mx-1">上传备份文件</el-text>
      <el-form-item label="" style="margin-top: 10px">
        <el-upload
            class="upload-demo"
            drag
            :http-request="handleFileUpload"
            multiple
            :show-file-list="true"
            :file-list="RecoverParams.fileList"
            style="width: 400px;"
        >
          <el-icon class="el-icon--upload">
            <upload-filled/>
          </el-icon>
          <div class="el-upload__text">
            Drop file here or <em>click to upload</em>
          </div>
          <template #tip>
            <div class="el-upload__tip">
              ⭐请上传备份压缩包文件
            </div>
          </template>
        </el-upload>
      </el-form-item>
      <el-form-item label="密钥">
        <el-input v-model="RecoverParams.encryptionKey"
                  placeholder="输入密钥（可选）" clearable style="width: 400px;"
                  type="password" show-password></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="handleRecover" style="width: 430px;" :loading="RecoverLoading">恢复数据
        </el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
import {UploadFilled} from '@element-plus/icons-vue'
import {ElMessage} from "element-plus";

export default {
  components: {
    UploadFilled
  },
  data() {
    return {
      BackupParams: {
        enableEnc: false,
        password: ''
      },
      RecoverParams: {
        encryptionKey: '',
        fileList: []
      },
      RecoverLoading: false
    };
  },
  methods: {
    async handleBackup() {
      try {
        const response = await this.$axios.post('/sys/backup', {...this.BackupParams}, {
          responseType: 'blob',
        });

        // 提取文件名
        let filename = 'backup.zip'; // 默认文件名
        const contentDisposition = response.headers['content-disposition'];

        if (contentDisposition) {
          const filenameRegex = /filename\*?=(?:.*'')?(.+)/;
          const matches = filenameRegex.exec(contentDisposition);
          if (matches && matches[1]) {
            filename = decodeURIComponent(matches[1]);
          }
        }

        // 创建 Blob 并触发下载
        const blob = new Blob([response.data], {type: 'application/octet-stream'});
        const url = window.URL.createObjectURL(blob);
        const link = document.createElement('a');
        link.href = url;
        link.download = filename;
        link.click();
        window.URL.revokeObjectURL(url);

        ElMessage({
          message: '备份文件已成功下载',
          type: 'success',
          duration: 2000,
        });
      } catch (error) {
        console.error('Error during backup:', error);
        ElMessage({
          message: '备份失败，请重试',
          type: 'error',
          duration: 2000,
        });
      }
    },
    // 自定义文件上传逻辑
    handleFileUpload({file}) {
      const reader = new FileReader();
      reader.onload = (e) => {
        const arrayBuffer = e.target.result;
        if (arrayBuffer) {
          this.RecoverParams.fileList.push(file);
        }
      };
      reader.readAsArrayBuffer(file); // 直接使用 file
    },
    async handleRecover() {
      this.RecoverLoading = true
      // 构造 FormData 对象
      const formData = new FormData();
      formData.append("encryptionKey", this.RecoverParams.encryptionKey);

      this.RecoverParams.fileList.forEach((file) => {
        formData.append("fileList", file);
      });
      // 发起 POST 请求
      await this.$axios.post("/sys/recover", formData, {
        headers: {
          "Content-Type": "multipart/form-data",
        },
      }).then(response => {
        if (response.data.success) {
          this.resetForm()
          ElMessage({
            message: response.data.msg,
            type: 'success',
            duration: 2000 // 设置提示显示时间，单位是毫秒
          });
        } else {
          this.RecoverLoading = false
          ElMessage({
            message: response.data.msg,
            type: 'error',
            duration: 2000 // 设置提示显示时间，单位是毫秒
          });
        }
      }).catch(error => {
        console.error('Error:', error)
      })
      this.resetForm()
    },
    resetForm() {
      this.BackupParams = {
        enableEnc: false,
        password: ''
      }
      this.RecoverParams = {
        encryptionKey: '',
        fileList: []
      }
      this.RecoverLoading = false
    },
  }
};
</script>

<style scoped>
.upload-demo {
  border: 2px dashed #d9d9d9;
  border-radius: 6px;
  padding: 20px;
  text-align: center;
  cursor: pointer;
}

</style>
