<template>
  <el-container style="height: 100vh;">
    <el-aside width="700px" class="left-panel">
      <el-form>
        <el-text class="mx-1" size="large">telegram机器人Token</el-text>
        <el-form-item style="margin-top: 10px">
          <el-input v-model="UpdateSysCfgParams.tgBotToken"
                    placeholder="12345678:AAGC8NFfxxxx"
                    clearable type="password" show-password></el-input>
        </el-form-item>
        <el-text class="mx-1" size="large">telegram个人ID</el-text>
        <el-form-item style="margin-top: 10px">
          <el-input v-model="UpdateSysCfgParams.tgChatId"
                    placeholder="61945xxxxx"
                    clearable type="password" show-password></el-input>
        </el-form-item>
        <el-text class="mx-1" size="large">钉钉access_token</el-text>
        <el-form-item style="margin-top: 10px">
          <el-input v-model="UpdateSysCfgParams.dingToken"
                    placeholder="0b0bf9f89xxxx"
                    clearable type="password" show-password></el-input>
        </el-form-item>
        <el-text class="mx-1" size="large">钉钉secret</el-text>
        <el-form-item style="margin-top: 10px">
          <el-input v-model="UpdateSysCfgParams.dingSecret"
                    placeholder="SEC1ebxxxx"
                    clearable type="password" show-password></el-input>
        </el-form-item>
      </el-form>

      <el-divider class="divider"/>

      <el-form>
        <!-- 新增开关 -->
        <el-form-item label="是否启用MFA（推荐谷歌）：" style="margin-top: 10px">
          <el-switch v-model="UpdateSysCfgParams.enableMfa" active-text="启用" inactive-text="禁用"/>
        </el-form-item>
        <el-text class="mx-1">⭐PS：如果禁用后重新启用，需要在APP上删除旧的验证码，重新扫码绑定新的MFA验证码❗❗❗</el-text>
        <!-- 条件显示图片 -->
        <div v-if="UpdateSysCfgParams.enableMfa" class="image-preview">
          <el-image
              style="margin-top: 20px; width: 300px; height: 300px"
              :src="UpdateSysCfgParams.mfaQrData"
              fit="contain"
          />
          <!-- 输入框 -->
          <el-input
              v-model="UpdateSysCfgParams.mfaSecret"
              style="margin-top: 10px; width: 400px"
              disabled
              placeholder=""
              label=""
          />
        </div>
      </el-form>
      <el-button type="primary" style="margin-top: 10px"
                 @click="updateSysCfg" :loading="cfgLoading">
        更新配置
      </el-button>
    </el-aside>
    <el-main class="right-panel">
      <el-input type="textarea" v-model="message" placeholder="输入测试文本" :rows="20"></el-input>
      <el-button type="primary" @click="sendMsg" :loading="sendLoading">测试消息发送</el-button>
    </el-main>
  </el-container>
</template>

<script>
import {ElMessage} from "element-plus";

export default {
  data() {
    return {
      cfgLoading: false,
      sendLoading: false,
      UpdateSysCfgParams: {
        dingToken: '',
        dingSecret: '',
        tgChatId: '',
        tgBotToken: '',
        enableMfa: '',
        mfaSecret: '',
        mfaQrData: ''
      },
      message: ''
    };
  },
  mounted() {
    this.getSysCfg()
  },
  methods: {
    async getSysCfg() {
      await this.$axios.post('/sys/getSysCfg', {})
          .then(response => {
            if (response.data.success) {
              // ElMessage({
              //   message: response.data.msg,
              //   type: 'success',
              //   duration: 2000 // 设置提示显示时间，单位是毫秒
              // });
              this.UpdateSysCfgParams = response.data.data
            } else {
              ElMessage({
                message: response.data.msg,
                type: 'error',
                duration: 2000 // 设置提示显示时间，单位是毫秒
              });
            }
          })
          .catch(error => {
            console.error('Error:', error)
          })
    },
    async updateSysCfg() {
      this.cfgLoading = true
      await this.$axios.post('/sys/updateSysCfg', {...this.UpdateSysCfgParams})
          .then(response => {
            if (response.data.success) {
              ElMessage({
                message: response.data.msg,
                type: 'success',
                duration: 2000 // 设置提示显示时间，单位是毫秒
              });
              this.cfgLoading = false
              this.getSysCfg()
            } else {
              ElMessage({
                message: response.data.msg,
                type: 'error',
                duration: 2000 // 设置提示显示时间，单位是毫秒
              });
            }
          })
          .catch(error => {
            console.error('Error:', error)
          })
    },
    async sendMsg() {
      this.sendLoading = true
      await this.$axios.post('/sys/sendMsg', {message: this.message})
          .then(response => {
            if (response.data.success) {
              ElMessage({
                message: response.data.msg,
                type: 'success',
                duration: 2000 // 设置提示显示时间，单位是毫秒
              });
              this.sendLoading = false
            } else {
              ElMessage({
                message: response.data.msg,
                type: 'error',
                duration: 2000 // 设置提示显示时间，单位是毫秒
              });
            }
          })
          .catch(error => {
            console.error('Error:', error)
          })
    }
  }
};
</script>

<style scoped>
.left-panel, .right-panel {
  display: flex;
  flex-direction: column;
  gap: 10px;
  padding: 20px;
}

.right-panel {
  flex: 1;
}

.el-input[type="textarea"] {
  flex: 1;
  resize: none;
}
</style>
